import React, {createContext, useContext, useEffect, useState} from "react"
import {useNavigate, useParams} from "react-router";
import {Game, useGame} from "../data/game";
import {Button, Dialog, DialogContent, DialogTitle, Stack, TextField, Typography} from "@mui/material";
import {DateTime} from 'luxon'
import {useFormik} from "formik";
import * as yup from "yup";

const TrackedGameContext = createContext<Game | null>(null)

export default () => {
    const navigate = useNavigate()
    const {gameId} = useParams()
    const {game} = useGame(gameId || null)
    const [started, setStarted] = React.useState(false)

    if (!game) {
        return <Typography variant="caption">Not found</Typography>
    }

    const handleStart = () => {
        setStarted(true)
        game.startGame()
    }

    const handleEnd = async () => {
        await game.save()
        navigate(`/games`)
    }

    return <Stack alignItems="center">
        <Typography variant="h6">Tracking for {game.name}</Typography>
        <TrackedGameContext.Provider value={game}>
            {
                !started
                    ? <Button variant='contained' onClick={handleStart}>Start Game</Button>
                    : null
            }
            {
                started
                    ? <GameTracker onEnd={handleEnd}/>
                    : null
            }
        </TrackedGameContext.Provider>
    </Stack>
}

function GameTracker(props: { onEnd: () => void }) {
    const game = useContext(TrackedGameContext)
    const gameStart = game?.startEvent ? DateTime.fromMillis(game.startEvent.timestamp) : null
    const [time, setTime] = useState('00:00')

    const [eventDialogOpened, setEventDialogOpened] = useState(false)

    useEffect(() => {
        if (gameStart) {
            const interval = setInterval(() => {
                const now = DateTime.local()
                const diff = now.diff(gameStart)
                setTime(diff.toFormat('mm:ss'))
            }, 1000)
            return () => clearInterval(interval)
        }
    })

    const handleEnd = () => {
        game?.endGame()
        props.onEnd?.()
    }

    const handleEventDialogSubmit = (gameEvent: string) => {
        game?.addEvent(gameEvent)
        setEventDialogOpened(false)
    }

    return <Stack spacing={3} alignItems="center">
        <Typography variant="h3">{time}</Typography>
        <Stack direction="column" spacing={2}>
            <Button variant="contained" color="error" onClick={handleEnd}>End</Button>
            <Button variant="contained" size="large" onClick={() => setEventDialogOpened(true)}>Event</Button>
            <NewEventDialog open={eventDialogOpened} onClose={() => setEventDialogOpened(false)} onSubmit={handleEventDialogSubmit} />
        </Stack>
    </Stack>
}

function NewEventDialog(props: {open: boolean, onClose: () => void, onSubmit: (event: string) => void}) {
    const formik = useFormik({
        initialValues: {
            name: '',
        },
        validationSchema: yup.object({
            name: yup.string().required('Required'),
        }),
        onSubmit: async values => {
            props.onSubmit(values.name)
        }
    })

    const handleCancel = () => {
        formik.resetForm()
        props.onClose()
    }

    return <Dialog open={props.open} onClose={props.onClose}>
        <DialogTitle>New Game Event</DialogTitle>
        <DialogContent>
            <Stack style={{marginTop: 2}}>
                <form onSubmit={formik.handleSubmit}>
                    <Stack direction="column" spacing={2} style={{paddingTop: 2}}>
                        <TextField id='name'
                                   label='Name'
                                   placeholder='Describe the event'
                                   value={formik.values.name}
                                   onChange={formik.handleChange}
                                   error={formik.touched.name && Boolean(formik.errors.name)}
                                   helperText={formik.touched.name && formik.errors.name}
                        />
                        <Button type="submit" variant="contained">Continue</Button>
                        <Button variant="outlined" onClick={handleCancel}>Cancel</Button>
                    </Stack>
                </form>
            </Stack>
        </DialogContent>
    </Dialog>
}